

















   import {
       mapState,
       mapMutations
   } from 'vuex'
   
   export default {
   	data() {
   		return {
   			
			ref:false
   		};
   	},
   	computed: {
   			...mapState(['isLogin','notice'])
   			},
	onLoad: function() {
		if (!this.isLogin) {
		var url="../../user/login/login";
		uni.navigateTo({
			url
		});
			}
	},
	onPullDownRefresh:function(){
		this.getNotice();
		uni.stopPullDownRefresh();
	},
	methods:{
		...mapMutations(['getNotice']),
	}
	}

